.gmcq {
  &__widget {
    display: flex;
    flex-wrap: wrap;
    //justify-content: center;
    flex: 0 1;
  }

  &__item {
    position: relative;
    width: 50%;
    
    margin-bottom: 1rem;
    @media screen and (max-width: 850px){
      width: 50% !important; 

    }
    @media screen and (max-width: 540px){
      width: 100% !important;
    }
  }
  .btn-reset{
    width: calc(50% - 1rem);
    @media screen and (max-width: 540px){
      width: calc(50% - 0.5rem);
    }
    @media screen and (max-width: 850px){
      width: calc(50% - .5rem);
    }
  } 
  .btn-submit{
    margin-right: .5rem;    
    @media screen and (max-width: 850px){
      width: calc(50% - .5rem);
    }
  }
  @media screen and (max-width: 850px){    
    .btn__container  {
      max-width: 100%;
    }    
  }

  &__item-input {
    position: absolute;
    bottom: @item-margin * 2;
    left: 50%;
    opacity: 0;
    z-index: -1;
    .transform(translate(-50%, 50%));
  }

  &__item-label {
    display: block;
    z-index: 1;
    height: 100%;
  }

  &__item-label:not(.is-disabled) {
    cursor: pointer;
  }

  &__item-icon {
    display: none;
  }

  // --------------------------------------------------
  // GMCQ item icons
  // --------------------------------------------------
  &__item-answer-icon.is-radio .icon {
    .icon-radio-unchecked;
  }

  &__item-answer-icon.is-checkbox .icon {
    .icon-checkbox-unchecked;
  }

  &__item-label.is-selected {
    .gmcq__item-answer-icon.is-radio .icon {
      .icon-radio-checked;
    }

    &.is-disabled{
      .gmcq__item-icon{
        display: none;
      }
  }
  }

  &__item-label.is-selected {
    .gmcq__item-answer-icon.is-checkbox .icon {
      .icon-checkbox-checked;
    }
  }

  &__item-correct-icon .icon {
    .icon-tick;
  }

  &__item-incorrect-icon .icon {
    .icon-cross;
  }
  // --------------------------------------------------

  // --------------------------------------------------
  // Class to show the selection icons until the component is complete
  // --------------------------------------------------
   &__widget:not(.is-complete) {
     .gmcq__item-answer-icon {
       display: block;
     }
   }
  // --------------------------------------------------

  // --------------------------------------------------
  // Class to show the users selection
  // --------------------------------------------------
  &__widget.show-user-answer {
    .is-correct .is-selected .gmcq__item-correct-icon,
    .is-incorrect .is-selected .gmcq__item-incorrect-icon {
      display: block;
    }
  }

  &__widget.show-user-answer {
    .gmcq__item:not(.is-correct):not(.is-incorrect) .is-selected .gmcq__item-answer-icon {
      display: block;
    }
  }
  // --------------------------------------------------

  // --------------------------------------------------
  // Class to show the correct answers
  // --------------------------------------------------
  &__widget.show-correct-answer {
    .is-correct .is-selected .gmcq__item-correct-icon,
    .is-incorrect .is-selected .gmcq__item-correct-icon {
      display: block;
    }
  }

  &__widget.show-correct-answer {
    .gmcq__item:not(.is-correct):not(.is-incorrect) .is-selected .gmcq__item-answer-icon {
      display: block;
    }
  }

  &__item-label.is-disabled  
   { 
     color: #ccc;   
     .gmcq__item-answer-icon {
       display: block;
//       opacity: 0.25;
    }

  
   
   }
   
   .is-incorrect_mark{
    .is-disabled{
      .gmcq__item-incorrect-icon{
        display: block;
      }
    }    
  }
  .is-correct_mark{
    .is-disabled{
      .gmcq__item-correct-icon{
        display: block;
      }  
    }
  }
  .show_correctmark_icons {
		.gmcq__item-icon{
		  display: none;
		}
		.gmcq__item-correct-icon {
		  display: block;
		}
	  }

	  .show_incorrectmark_icons {
		.gmcq__item-icon{
		  display: none;
		}
		.gmcq__item-incorrect-icon {
		  opacity: 1;
		  display: block;
		}

	  }

    .is-option-selected{
      pointer-events: none;
      background-color: #f4f4f4 !important;
      .gmcq__item-icon{
        display: none;      
      }
      .gmcq__item-incorrect-icon{
        display: block;      
      }
    } 

    .is-disabled-option .gmcq__item-label.is-disabled{
    //  background-color: #f4f4f4 !important;
      pointer-events: none;
    }
    
   .is_disabled_btn {
		pointer-events: none;
		background-color: @greyLight;
		border-color: @greyLight; 
		color: #000000;
    }
   
  // --------------------------------------------------
}
.component__body-inner {
  font-size: 1rem !important;
}
