.matching {
  &__item {
    margin-bottom: @item-margin / 2;
  }

  &__item-title {
    padding: @item-padding;
  }

  &__select-container {
    position: relative;
  }

  &__select-state {
    position: absolute;
    top: 50%;
    right: @item-padding;
    .transform(translateY(-50%));

    .dir-rtl & {
      right: inherit;
      left: @item-padding;
    }
  }

  &__widget.is-disabled {
    .matching__select-icon {
      color: @disabled-inverted;
    }
  }
  // --------------------------------------------------
  // Error highlight
  // --------------------------------------------------
  &__widget:not(.is-complete) .matching__select-container.has-error .dropdown__btn {
    box-shadow: 0 0 0 1px @validation-error inset;
  }
  // --------------------------------------------------
}
.matching__select-incorrect-icon .icon:before{
  background-image: url("course/en/images/mcq_cross_icon.png");
  height: 24px;
  width: 24px;
  background-size: contain;
  background-repeat: no-repeat;
  content: "";
}
.matching__select-correct-icon .icon:before{
  background-image: url("course/en/images/mcq_tick_icon.png");
  height: 24px;
  width: 24px;
  background-size: contain;
  background-repeat: no-repeat;
  content: "";
}
.dropdown__item {
  padding: 1rem;
  background-color: white;
  border-bottom: 1px solid rgba(0,0,0,0);
  color: #58595b;
  border-bottom: 1px solid #d6d6d6;
  border-right: 1px solid #d6d6d6;
  border-left: 1px solid #d6d6d6;
}
.dropdown__item :hover {
  color: black;
}
.dropdown__btn {

  color: #58595b !important;
}
.no-touch .dropdown__item:not(:focus):not([aria-selected="true"]):hover {
  background-color: rgb(255 255 255);
  color: #58595b;
  transition: background-color .2s ease-in,color .2s ease-in;
}

