
.article {
  position: relative;
}

.trickle {
  &__inner {
    z-index: 80;
  }

  &__btn {
    display: block;
    margin: auto;
  }

  &.is-full-width .trickle__btn {
    width: auto;
    min-width: 200px;
  }

  &__btn-icon .icon {
    .icon-controls-small-down;
  }

  // --------------------------------------------------
  // Adds the necessary styling to lock the button
  // --------------------------------------------------
  &.is-locked {
    // position: absolute;
    // bottom: 0;
    // left: 0;
    // right: 0;
  }
  // --------------------------------------------------

  // --------------------------------------------------
  // If full width config is enabled, button is fixed to the bottm of the screen
  // --------------------------------------------------
  &.is-full-width .trickle__inner {
    //position: absolute;
    bottom: 150px;
    left: 0;
    right: 0;
    @media screen and(max-width: 850px) {
      bottom: 50px;
    }
  }
  .is-final{
    display: none !important;
  }
  .is-hide{
    display: none;
  }
  .is-show{
    display: block;
    font-size: 0.9rem;
    max-width: 350px;
    @media screen and (max-width: 850px){
      font-size: 0.8rem;
      padding: 0 0.8rem;
    }
  }
  // --------------------------------------------------
}