.block {
  &.has-bg-image {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
  }


 
  &__inner {
   // .l-container-padding(@block-padding-ver, @block-padding-hoz);
   .l-container-padding(1rem, 1rem);
   height: auto;
   min-height: 430px;

   //overflow: auto;

   @media screen and (max-width: 767px){       
    padding-bottom: 20px;
     height: auto;
     
   }
  }
  
  &__title {
    margin-bottom: @block-title-margin;
    .block-title;
    color: @heading-color;
  }

  &__body {
    margin-bottom: @block-body-margin;
  }

  &__instruction {
    margin-bottom: @block-instruction-margin;
    .instruction-text;
  }

  &__body a,
  &__instruction a {
    .link-text;
  }
}
.block__container > .block{
  min-height: 100vh;  
}