.mcq {
  &__item {
    position: relative;
  }

  &__item-input {
    position: absolute;
    top: 50%;
    left: 50%;
    opacity: 0;
    z-index: -1;
    .transform(translate(-50%, -50%));

    &:focus+label {
      outline: 1px solid #37bdff;
    }
  }

  &__item-label {
    display: block;
    z-index: 1;
  }

  &__item-label:not(.is-disabled) {
    cursor: pointer;
  }

  &__item-icon {
    display: none;
    //opacity: 0.5;
  }

  // --------------------------------------------------
  // MCQ item icons
  // --------------------------------------------------
  &__item-answer-icon.is-radio .icon {
    .icon-radio-unchecked;
  }

  &__item-answer-icon.is-checkbox .icon {
    .icon-checkbox-unchecked;
  }

  &__item-label.is-selected {
    .mcq__item-answer-icon.is-radio .icon {
      .icon-radio-checked;

    }

    &.is-disabled {
      .mcq__item-icon {
        display: none;
      }
    }
  }

  &__item-label.is-selected {
    .mcq__item-answer-icon.is-checkbox .icon {
      .icon-checkbox-checked;
    }
  }

  &__item-correct-icon .icon {
    .icon-tick;
  }

  &__item-incorrect-icon .icon {
    .icon-cross;
  }

  // --------------------------------------------------

  // --------------------------------------------------
  // Class to show the selection icons until the component is complete
  // --------------------------------------------------
  &__widget:not(.is-complete) {
    .mcq__item-answer-icon {
      display: block;
    }
  }

  // --------------------------------------------------

  // --------------------------------------------------
  // Class to show the users selection
  // --------------------------------------------------
  &__widget.show-user-answer {

    .is-correct .is-selected .mcq__item-correct-icon,
    .is-incorrect .is-selected .mcq__item-incorrect-icon {
      display: block;
    }
  }

  &__widget.show-user-answer {
    .mcq__item:not(.is-correct):not(.is-incorrect) .is-selected .mcq__item-answer-icon {
      display: block;
    }
  }

  // --------------------------------------------------

  // --------------------------------------------------
  // Class to show the correct answers
  // --------------------------------------------------
  &__widget.show-correct-answer {

    .is-correct .is-selected .mcq__item-correct-icon,
    .is-incorrect .is-selected .mcq__item-correct-icon {
      display: block;
    }
  }

  &__widget.show-correct-answer {
    .mcq__item:not(.is-correct):not(.is-incorrect) .is-selected .mcq__item-answer-icon {
      display: block;
    }
  }

  // --------------------------------------------------

  &__item-label.is-disabled {
    //color: #ccc;

    .mcq__item-answer-icon {
      display: block;      
    }



  }

  .is-incorrect_mark {
    .is-disabled {
      .mcq__item-incorrect-icon {
        display: block;
      }
    }
  }

  .is-correct_mark {
    .is-disabled {
      .mcq__item-correct-icon {
        display: block;
      }
    }
  }

  .show_correctmark_icons {
    .mcq__item-icon{
      display: none;
    }
    .mcq__item-correct-icon {
      display: block;
    }
  }

  .show_incorrectmark_icons {
    .mcq__item-icon{
      display: none;
    }
    .mcq__item-incorrect-icon {
      opacity: 1;
      display: block;
    }

  }

  .btn-reset{
    width: 50% !important;   
  } 
  .component__body-inner{
    font-size: 1rem;
  }



  .is_disabled_btn {
    pointer-events: none;
    background-color: @greyLight;
    border-color: @greyLight;
    color: #000000;
  }


  .is-option-selected{
    pointer-events: none;
    background-color: #f4f4f4 !important;
    .mcq__item-icon{
      display: none;      
    }
    .mcq__item-incorrect-icon{
      display: block;      
    }
  } 
 
  .is-disabled-option{
   // background-color: #f4f4f4 !important;
    pointer-events: none;
  }
}
.MMCQ_feedback .mmcq-dflex{
display: flex;
}
.MMCQ_feedback .mcq__item{
  width: 50%;
  }
  .MMCQ_feedback .mmcq__item-label-feedback{
    width: 50%;
   
    }
    .mcq__item-feedback{
      color: #d13735;
      font-weight: bold;
      margin: 10px;
    }
    @media screen and (max-width: 668px) {
      .MMCQ_feedback .mmcq-dflex{
        display: block;
        }
        .MMCQ_feedback .mcq__item{
          width: 100%;
          }
          .MMCQ_feedback .mmcq__item-label-feedback{
            width: 100%;
           
            }
    }