  // -------------------------------------------------
  // Layout #1
  // --------------------------------------------------
  &__item-option {
    margin-top: @item-margin;
    padding: @item-padding / 2;
    background-color: @item-color;
    color: @item-color-inverted;
    border-radius: @item-border-radius;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 85px;
  }

  .no-touch &__item-label:not(.is-disabled):not(.is-selected):hover .gmcq__item-option,
  html:not(.a11y-disable-focusoutline) .gmcq__item-input:focus + &__item-label &__item-option {
    //background-color: @item-color-hover;
    color: @item-color-inverted-hover;
    .transition(background-color @duration ease-in, color @duration ease-in;);
  }

  &__item-label.is-disabled .gmcq__item-option {
    //background-color: @disabled;
    color: @item-color-inverted-selected;
  }

  &__item-label.is-selected .gmcq__item-option {
    //background-color: @item-color-selected;
    color: @item-color-inverted-selected;
  }

  &__item-state {
    min-height: @icon-size;
    min-height: 1.5rem;
    margin-right: 10px;
    min-width: 24px;
  }

  &__item-text {
    //margin-top: @item-margin;
    text-align: left;
  }
    &__body-inner {
    position: relative;
    padding-left: 24px;
    font-size: 1rem;
    &:before {
      content: "Q.";
      position: absolute;
      left: 0;
      font-weight: 700;
      color: #806186;
    }
  }

  &__item-correct-icon {
    color: #39a23e;
  }
  &__item-incorrect-icon {
    color: red;
  }
  &__item-answer-icon.is-radio .icon:before{
    background-image: url("course/en/images/mcq_radio_normal.png");
    height: 24px;
    width: 24px;
    background-size: contain;
    background-repeat: no-repeat;
    content: "";
  }
  &__item-label.is-selected .gmcq__item-answer-icon.is-radio .icon:before{
    background-image: url("course/en/images/mcq_radio_select.png");
    content:"";
  }
  &__item-incorrect-icon .icon:before{
    
    background-image: url("course/en/images/mcq_cross_icon.png");
    height: 18px;
    width: 18px;
    background-size: contain;
    background-repeat: no-repeat;
    content: "";
  }
  &__item-correct-icon .icon:before{    
    background-image: url("course/en/images/mcq_tick_icon.png");
    height: 24px;
    width: 24px;
    background-size: contain;
    background-repeat: no-repeat;
    content: "";
  
  }
  .is-disabled label{
    cursor: pointer;
    &.is-selected{
      cursor: default;
    }
  } 
  .is-reseted .is-disabled label {
    cursor: default;
  }
