.mcq {
  &__item-label {
    margin-bottom: @item-margin;
    background-color: @item-color;
    color: #58595b;  
    // border-radius: @item-border-radius;
    border: 1px solid #d1d3d4;

    .no-touch &:not(.is-disabled):not(.is-selected):hover,
    html:not(.a11y-disable-focusoutline) .mcq__item-input:focus + & {
      background-color: @item-color-hover;
      color: #58595b;
      .transition(background-color @duration ease-in, color @duration ease-in;);
    }

    .no-touch &:not(.is-disabled):not(.is-selected):hover .mcq__item-icon {
      .effect-scalePulse;
    }
  }


  &__item-label.is-disabled {
    background-color: transparent;;
    color: #58595b;
  }
  

  &__item-label.is-selected {
    background-color: #f4f4f4;
    color: #58595b !important;
  }

  &__item-state {
    position: absolute;
    top: 50%;
    left: @item-padding;
    .transform(translateY(-50%));

    .dir-rtl & {
      left: inherit;
      right: @item-padding;
    }
  }

  &__item-text {
    padding: 0.5rem .5rem 0.5rem 3.5rem;
    // 0.75rem 1rem 0.75rem 3.5rem    

    .dir-rtl & {
      padding-left: @item-padding;
      padding-right: @icon-size + (@item-padding * 2);
    }
  }

  &__body-inner {
    position: relative;
    padding-left: 24px;
    &:before {
      content: "Q.";
      position: absolute;
      left: 0;
      font-weight: 700;
      color: #806186;
    }
  }  
  &__item-correct-icon {
    color: #39a23e;
  }
  &__item-incorrect-icon {
    color: red;
  }
  &__item-answer-icon.is-radio .icon:before{
    background-image: url("course/en/images/mcq_radio_normal.png");
    height: 24px;
    width: 24px;
    background-size: contain;
    background-repeat: no-repeat;
    content: "";
  }
  &__item-label.is-selected .mcq__item-answer-icon.is-radio .icon:before{
    background-image: url("course/en/images/mcq_radio_select.png");
    content:"";
  }
  &__item-incorrect-icon .icon:before{
    
    background-image: url("course/en/images/mcq_cross_icon.png");
    height: 20px;
    width: 20px;
    background-size: contain;
    background-repeat: no-repeat;
    content: "";
  }
  &__item-correct-icon .icon:before{    
    background-image: url("course/en/images/mcq_tick_icon.png");
    height: 20px;
    width: 20px;
    background-size: contain;
    background-repeat: no-repeat;
    content: "";
  }
  .is-disabled label{
    cursor: pointer;
    &.is-selected{
      cursor: default;
    }
  } 
  .is-reseted .is-disabled label {
    cursor: default;
  } 
  

}

