.matching {
  &__select-icon {
    display: none;
  }

  // --------------------------------------------------
  // Matching item icons
  // --------------------------------------------------
  &__select-correct-icon .icon {
    .icon-tick;
  }

  &__select-incorrect-icon .icon {
    .icon-cross;
  }
  // --------------------------------------------------

  // --------------------------------------------------
  // Class to show the users selection
  // --------------------------------------------------
  &__widget.show-user-answer {
    .is-correct .matching__select-correct-icon,
    .is-incorrect .matching__select-incorrect-icon {
      display: block;
    }
  }
  
  // --------------------------------------------------

  // --------------------------------------------------
  // Class to show the correct answers
  // --------------------------------------------------
  &__widget.show-correct-answer {
    .is-correct .matching__select-correct-icon,
    .is-incorrect .matching__select-correct-icon {
      display: block;
    }
  }
  // --------------------------------------------------
}

.matching__item-title {
  padding: 0rem !important;
}
.dropdown__btn {
 
  background-color: #f5f5f5 !important;
  color: #000 !important;
  border: 1px solid #d6d6d6 !important;
}

.matching__body-inner:before {
  content: "Q.";
  position: absolute;
  left: 0;
  font-weight: 700;
  color: #806186;
}
.matching__body-inner {
  position: relative;
  padding-left: 24px;
}