.d-flex{
    display: flex;
    &.v-center{
        align-items: center;
        justify-content: center;
    }
    &.j-center{
        justify-content: center;
    }
    &.j-flex-end{
        justify-content: flex-end;
    }
    &.dir-col{
        flex-direction: column;
    }
}

.order-mobile{
    &-1{
    @media screen and (max-width: 800px){
        order: 1;
     }        
    }
    &-2{
        @media screen and (max-width: 800px){
        order: 2;
        }
    }
    &-3{
        @media screen and (max-width: 800px){
        order: 3;
        }
    }
}



.max-w-350{
    max-width: 350px;
}

.min-h-100{
    min-height: 100vh;
}
.mx-w-100{
    max-width: 100%;
}

.w{
    &-30{
        width: 30%;
    }
    &-40{
        width: 40%;
        @media screen and (max-width: 800px){
            width: 100%;
        }        
    }
    &-50{
        width: 50%;
        @media screen and (max-width: 800px){
            width: 100%;
        }  
    }
    &-60{
        width: 60%;
        @media screen and (max-width: 800px){
            width: 100%;
        }  
    }
    &-70{
        width: 70%;
        @media screen and (max-width: 800px ){
            width: 100%;
        } 
         
    }
    &-45{
        width: 45%;
        @media screen and (max-width: 800px ){
            width: 100%;
        } 
         
    }
    &-55{
        width: 55%;
        @media screen and (max-width: 800px ){
            width: 100%;
        } 
         
    }
}

.w-mobile{
    &-50{
        width: 100%;
        @media screen and (max-width: 800px){
            width: 50% !important;
        }
    }
}


.p{
    &-l{
        &-3p{
            padding-left: 3%;
        }
    }
    &t-100{
        padding-top: 100px;
    }
}

.text{
    &-center{
        text-align: center;
    }
    &-right{
        text-align: right;
    }
    &-left{
        text-align: left;
    }
    
}

.container-position{
    &-right{
        float: right;        
    }
    &-left{
        float: left;        
    }
    
}

.disable_btn{
    background-color: #ccc !important;
    color: #fff !important;
    pointer-events: none !important;
    cursor: default !important;
}
.overflow_hidden{
    overflow: hidden !important;
}
.pd-right{
        padding-right: 1.5rem;
      }
    

